  .nav-links {
    list-style-type: none;
    display: flex;
    /* justify-content: center; Center the items horizontally */
    /* align-items: center; Center the items vertically */
    margin: 0;
    padding: 0;
    
  }
  
  .nav-links li {
    margin-right: 20px;
  }
  
  .nav-links a {
    text-decoration: none;
    color: white;
  }
  
  .nav-links a:hover {
    text-decoration: underline;
  }
  .footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 20px 0;
    position: fixed;
    bottom: 0;
    width: 100%;
}

.footer p {
    margin: 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}
